<h2 id="space-间距" tabindex="-1"><a class="header-anchor" href="#space-间距" aria-hidden="true">#</a> Space 间距</h2>
<p>设置组件之间的间距</p>
<h2 id="何时使用" tabindex="-1"><a class="header-anchor" href="#何时使用" aria-hidden="true">#</a> 何时使用</h2>
<p>避免组件紧贴在一起，拉开统一的空间。</p>
<ul>
<li>适合行内元素的水平间距。</li>
<li>可以设置各种水平对齐方式。</li>
</ul>
<h2 id="用法" tabindex="-1"><a class="header-anchor" href="#用法" aria-hidden="true">#</a> 用法</h2>
<p>基本用法</p>
<wd-space>
<div v-for="o in 4" :key="o" class="text item">
    {{ 'List item ' + o }}
</div>
</wd-space>
::: details 点击查看代码 
<wd-space wrap :size="[10,20]" align="center" style="width: 100%" spacer="|">
            <div v-for="(item, index) in 20">
                <wd-button>{{ item }}</wd-button>
            </div>
</wd-space>
:::
属性
参数	说明	类型	可选值	默认值	
align	对齐的方式	string	start / end / center / baseline	center	
style	额外样式	string/object	-	-	
spacer	间隔符	string  /  number  /  VNode	-	-	
size	间隔大小	number  /  array	-	small	